$slider: #b5e7a0;
$handle: #86af49;
$brd: #e3eaa7;
.example {
	display: grid;
	grid-gap: 50px; }
.js-test {
	width: 80%;
	height: 500px;
	padding: 10px;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	justify-content: space-around;
	.inputPanel {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		flex-direction: column;
		margin-left: 2rem;
		input {
			order: -1; } } }
.slider {
	position: relative;
	user-select: none;
	width: 100%;
	height: 20px;
	background: $slider;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	align-items: center;
	.axis {
		width: 100%;
		position: relative;
		.range {
			position: absolute;
			height: 20px;
			background: $handle;
			transform-origin: 0 50%;
			transform: translate(0,50%); }
		.handle {
			position: absolute;
			left: 0;
			width: 20px;
			height: 20px;
			background: $handle;
			border: 5px solid $brd;
			border-radius: 50%;
			transform-origin: 50% 0;
			transform: translate(-50%,50%);
			.title {
				background: $brd;
				position: absolute;
				left: 50%;
				top: -120%;
				transform-origin: 50% 50%;
				transform: translate(-50%,0); } } } }
.slider.vertical {
	width: 20px;
	height: 100%;
	justify-content: center;
	.axis {
		height: 100%;
		width: 0;
		position: relative;
		.range {
			transform-origin: 50% 0;
			width: 20px;
			height: 0;
			transform: translate(-50%,0); }
		.handle {
			bottom: 0;
			transform: translate(-50%,50%);
			.title {
				background: $brd;
				position: absolute;
				left: 150%;
				top: -50%;
				transform: translate(0,50%); } } } }
.config {
	white-space: pre; }
.slider {
	.scale {
		height: 2px;
		width: 100%;
		position: absolute;
		left: 0;
		top: 200%;
		background: $slider;
		p {
			position: absolute;
			margin: 0;
			transform-origin: 0 0;
			bottom: 0;
			transform: translate(0, 100%) rotate(45deg); } } }
.slider.vertical {
	margin-left: 20px;
	margin-top: 20px;
	.scale {
		height: 100%;
		width: 2px;
		left: -100%;
		top: 0;
		p {
			transform: translate(-100%, 0); } } }
